/* Tailwind CSS directives - v3 syntax */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* frontend/src/index.css */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap');

:root {
  --color-background: #000000;
  --color-surface-primary: #1A1C2A;
  --color-surface-secondary: #2C2F48;
  --color-border: #4A4E88;

  /* --- CHANGE FROM BLUE TO PURPLE --- */
  --color-primary-accent: #8A2BE2;
  /* Vibrant Violet/Purple */
  --color-primary-accent-hover: #9932CC;
  /* Lighter Violet */
  --color-gold-accent: #FFD700;

  --color-text-primary: #F0F2F5;
  --color-text-secondary: #A0A3BD;
  --color-error: #E53E3E;
  /* Sharp red for errors */

  /* Typography */
  --font-family-heading: 'Orbitron', sans-serif;
  --font-family-body: 'Inter', sans-serif;

  /* Other Variables */
  --border-radius: 6px;
  --spacing-unit: 8px;
  --transition-speed: 0.2s;
  --glow-shadow: 0 0 8px var(--color-mana-blue), 0 0 12px var(--color-mana-blue);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-family-body);
  background-color: var(--color-background);
  color: var(--color-text-primary);
  line-height: 1.6;
}

h1,
h2,
h3,
h4 {
  font-family: var(--font-family-heading);
  color: var(--color-text-primary);
  margin-bottom: calc(var(--spacing-unit) * 2);
  font-weight: 700;
}

.App>h1 {
  text-align: center;
  font-size: 2.5rem;
  color: var(--color-mana-blue);
  text-shadow: 0 0 5px var(--color-mana-blue);
  margin-bottom: calc(var(--spacing-unit) * 4);
}

.form-input,
.form-select,
.form-textarea {
  width: 100%;
  background-color: rgba(13, 14, 20, 0.5);
  /* Semi-transparent dark background */
  color: var(--color-text-primary);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: calc(var(--spacing-unit) * 1.5);
  font-family: var(--font-family-body);
  font-size: 1rem;
  transition: all var(--transition-speed) ease;
}

.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--color-primary-accent);
  box-shadow: var(--glow-shadow);
}

.App {
  max-width: 1380px;
  margin: 0 auto;
  padding: var(--space-lg) var(--space-xl);
  /* <-- Add horizontal padding */
}

.site-wrapper {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 var(--space-xl);
}

::selection {
  background-color: var(--color-primary-accent);
  /* Use your theme's purple */
  color: var(--color-text-primary);
}